* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #728a97;
    color: #212121;
    line-height: 1.5;
  }
  
  #search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
    background-color: #a6c5d6;
  }
  
  .country-list,
  .country-info {
    width: fit-content;
    background-color: #a6c5d6;
  }